<template>
    <div class="container">
        <h2>列车时刻表</h2>
        <div class="subway">
            <div class="way1" :class="{ rise : active[0] }" @click="Sway1()">
                <img src="../../assets/images/subway1.svg" alt="">
                <span>1号线</span>
            </div>
            <div class="way2" :class="{ rise : active[1] }" @click="Sway2()">
                <img src="../../assets/images/subway2.svg" alt="">
                <span>2号线</span>
            </div>
        </div>
        <img id="runtime" src="../../assets/images/runtime1.png" alt="">
    </div>
</template>
<script>
export default {
    name : "Time",
    data() {
        return {
            active : [true,false],
        }
    },
    methods : {
        Sway1 : function() {
            this.active = [];
            this.active.push(true);
            var image = document.getElementById("runtime");
            image.src = require("../../assets/images/runtime1.png");
        },
        Sway2 : function() {
            this.active = [];
            this.active.push(false);
            this.active.push(true);
            var image = document.getElementById("runtime");
            image.src = require("../../assets/images/runtime2.png");
        }
    }
}
</script>
<style scoped>
@keyframes move {
    0%{opacity: 0;}
    100%{opacity: 1;}
}
h2 {
    /* text-align: center; */
    color: #fff;
    background-image: url(../../assets/images/title.svg);
    background-size: cover;
    width: 20%;
    margin: 0 auto;
    animation: move 2s;
}
.container {
    text-align: center;
}
.subway {
    padding-top: 230px;
    text-align: center;
    position: relative;
}
.subway span {
    display: block;
    font-size: 24px;
}
.subway .rise {
    transition: top 0.3s ease-in-out;
    top: 70px !important;
}
.subway .way1 {
    position: absolute;
    top: 150px;
    left: 35%;
}
.subway .way2 {
    position: absolute;
    top: 150px;
    right: 35%;
}
#runtime {
    margin-top: 100px;
}
</style>